* {
    margin: 0;
    padding: 0;
}

html,
body {
    overflow: hidden;
   
}

.canvas {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
    width: 1000px;
    height: 500px;
    background-image: url('/back.jpg');
    background-repeat: no-repeat;
    background-size: 15%;
    background-position: center;
    background-color: black;


}
